import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Alert from '@vkontakte/vkui/dist/components/Alert/Alert';
import Dasha from '../img/Dasha.png';

import VacantDay from '../img/VacantDay.png';
import './RecordMenuStyle.css';
import { render } from 'react-dom';


const osName = platform();

var count=4;


function RecordSelection(props){
	return (
		<Button className = "ButtonDataTime" size="l" mode="outline" onClick = {(e) => props.confirm(e.currentTarget.dataset.to)} data-to= {props.time}>
			<Div className = "TimeRecord">{props.time}</Div>
			<Div className = "CountRecord">{count}</Div>
		</Button>
	)
}

function ButtonDayRecord(props){
	return(
		<Button className="ButtonMothsDay" size="l" mode="outline" onClick = {props.changeOfDay}>
			<div className="FontClass">{props.day}</div>
		</Button>
	)
}


function ButtonsForDay(props){
	return(
		<Div className="RecordBlockDataTime" id = {props.day}>
			<RecordSelection time ={props.day} confirm = {props.confirm}/>
			<RecordSelection time ="16:00" confirm = {props.confirm}/>
			<RecordSelection time ="18:00" confirm = {props.confirm}/>
			<RecordSelection time ="20:00" confirm = {props.confirm}/>
			<RecordSelection time ="22:00" confirm = {props.confirm}/>
		</Div>
	)
}


class ActiveButtons extends React.Component {
	constructor(props) {
	  super(props);
	  this.state = {
		  day : "ПН",
		  isConfirm : false,
		  countWashMachine :0,
		  time : ""
		};
	  this.handleClick = this.handleClick.bind(this);
	  this.handleClickOnDataTime = this.handleClickOnDataTime.bind(this);
	  this.handleClickOnCountWashMashines = this.handleClickOnCountWashMashines.bind(this);
	  this.handleClickOnDataTimeEvent = this.handleClickOnDataTimeEvent.bind(this);
	}
	handleClick(newDay) {
		this.setState(
			{
				day: newDay
		});
	  }
	handleClickOnDataTime(){
		this.setState(state =>(
			{
				isConfirm : !state.isConfirm,
		}));
	}
	handleClickOnDataTimeEvent(newTime){
		this.setState(state =>(
			{
				isConfirm : !state.isConfirm,
				time : newTime
		}));
		console.log(newTime);
	}
	handleClickOnCountWashMashines(count){
		console.log(this.state.day);
		console.log(count);
		this.setState(
			{
				countWashMachine: count
		});
	}
	render() {
		var alertConfirm = this.state.isConfirm ? <AlertConfirm close = {() => this.handleClickOnDataTime()} changeCount = {this.handleClickOnCountWashMashines} />: null;
		return (
			<Div>
				<Div className= "RecordBlock">
				<ButtonDayRecord day = "ПН" changeOfDay = {() => this.handleClick("ПН")}/>
				<ButtonDayRecord day = "ВТ" changeOfDay = {() => this.handleClick("ВТ")}/>
				<ButtonDayRecord day = "СР" changeOfDay = {() => this.handleClick("СР")}/>
				<ButtonDayRecord day = "ЧТ" changeOfDay = {() => this.handleClick("ЧТ")}/>
				<ButtonDayRecord day = "ПТ" changeOfDay = {() => this.handleClick("ПТ")}/>
				<ButtonDayRecord day = "СБ" changeOfDay = {() => this.handleClick("СБ")}/>
				<ButtonDayRecord day = "ВС" changeOfDay = {() => this.handleClick("ВС")}/>
				</Div>
				<ButtonsForDay day = {this.state.day} confirm = {this.handleClickOnDataTimeEvent}/>
				{alertConfirm}
			</Div>
		);
	}
}

function AlertConfirm(props){
	return(
	<Alert actionsLayout = "vertical" onClose = {props.close} >
		<Div className = "TextNotify">Выберите нужное количество машинок</Div>
		<Div className = "ButtonsCountWashingMachines">
			<Button className = "ButtonWashMachine" size="l" mode="outline" onClick = {() => props.changeCount(1)}>1</Button>
			<Button className = "ButtonWashMachine" size="l" mode="outline" onClick = {() => props.changeCount(2)}>2</Button>
			<Button className = "ButtonWashMachine" size="l" mode="outline" onClick = {() => props.changeCount(3)}>3</Button>
			<Button className = "ButtonWashMachine" size="l" mode="outline" onClick = {() => props.changeCount(4)}>4</Button>
		</Div>
		<Button className = "ConfirmRecord" size="xl" mode="outline" onClick = {props.close}>Потвердить</Button>
  	</Alert>
	)
}

const RecordMenu = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={props.go} data-to="mainMenu">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}>Laundry</PanelHeader>
		<Div className="MainRecord">
			<Div className= "TextBlockRecord">
				Выберите время, дату и количество машинок
			</Div>
			<ActiveButtons />
			<Div className = "ButtonBlocksRecord">
				<Button className="ButtonBlcokRecord" size="xl" mode="outline">
					<Div className = "FontButtonRecord">ЗАПИСАТЬСЯ</Div>
				</Button>
				<Button className="ButtonBlcokRecord" size="xl" mode="outline" onClick={props.go} data-to="mainMenu">
					<Div className = "FontButtonRecord">НАЗАД</Div>
				</Button>
			</Div>
		</Div>
	</Panel>
);

RecordMenu.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default RecordMenu;
