import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Button from '@vkontakte/vkui/dist/components/Button/Button';

import Home from '../img/Home.png';
import './MainMenuStyle.css';

const osName = platform();

var name1 = "Влад";

function ChangeOfName(props) {
	if(props.name === "Дарья"){
		return "самая красивая и милая Даша)";
	}
  return props.name;
}

const MainMenu = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={props.go} data-to="startMenu">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}>Laundry</PanelHeader>
		<Div className = "MainMenu">
			<Div className ="TextBlockMain">
				Добрый день, <ChangeOfName name = {props.fetchedUser.first_name} />
			</Div>
			<Div>
				<img className="ImgHome" src={Home}></img>
			</Div>
			<Div className="TextBlockMainSecond">
				Нет предстоящих стирок
			</Div>
			<Div className = "ButtonBlocksMain">
				<Button className="ButtonBlcokMain" size="xl" mode="outline" onClick={props.go} data-to="recordMenu">
					<Div className= "FontButtonMain">ЗАПИСАТЬСЯ НА СТИРКУ</Div>
				</Button>
				<Button className="ButtonBlcokMain" size="xl" mode="outline" onClick={props.go} data-to="codeMenu">
					<Div className= "FontButtonMain">МОЙ КОД ДОСТУПА</Div>
				</Button>
			</Div>
		</Div>
	</Panel>
);

MainMenu.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default MainMenu;
